import React from "react"
import { Link } from "gatsby"

import { Container, Row, Col } from "reusecore/Layout"

import { FiMail, FiPhoneCall } from "react-icons/fi"
import { FaFacebookF, FaTwitter, FaLinkedinIn } from "react-icons/fa"

import logo from "assets/images/app/logo.png"
import particleOne from "assets/images/app/footer/footer-particle.png"

import FotterWrapper from "./footer.style"

const Footer = () => {
  return (
    <FotterWrapper>
      <img
        src={particleOne}
        className="section__particle one"
        alt="appion app landing"
      />
      <Container>
        <Row>
          <Col xs={12} sm={6} lg={3}>
            <div className="footer-widgets first">
              <ul className="info">
                <li>
                  <FiMail />
                  <Link className="anchor" to="#">
                    info@axcer.io
                  </Link>
                </li>
                <li>
                  <FiPhoneCall />
                  <Link className="anchor" to="#">
                    +94 7182 17615
                  </Link>
                </li>
              </ul>

              <ul className="social">
                <li>
                  <Link className="anchor" to="https://www.facebook.com/AxcerInnovations/">
                    <FaFacebookF />
                  </Link>
                </li>
                {/* <li>
                  <Link className="anchor" to="#">
                    <FaTwitter />
                  </Link>
                </li> */}
                <li>
                  <Link className="anchor" to="https://www.linkedin.com/company/axcer">
                    <FaLinkedinIn />
                  </Link>
                </li>
              </ul>
            </div>
          </Col>
          <Col xs={12} sm={6} lg={3}>
            <div className="footer-widgets">
              <h3 className="widget-title">Services</h3>
              <ul className="widget-catagory">
                <li>
                  <Link className="anchor" to="#">
                    Web Developments
                  </Link>
                </li>
                <li>
                  <Link className="anchor" to="#">
                    Software Development
                  </Link>
                </li>
                <li>
                  <Link className="anchor" to="#">
                    Contact
                  </Link>
                </li>
              </ul>
            </div>
          </Col>
          <Col xs={12} sm={6} lg={3}>
            <div className="footer-widgets">
              <h3 className="widget-title">About Us</h3>
              <ul className="widget-catagory">
                <li>
                  <Link className="anchor" to="#">
                    About Us
                  </Link>
                </li>
              </ul>
            </div>
          </Col>
          <Col xs={12} sm={6} lg={3}>
            <div className="footer-widgets">


            </div>
          </Col>
        </Row>
        <div className="footer-bottom">
          <ul className="footer-menu">
            <li>
              <Link className="anchor" to="#">
                Terms
              </Link>
            </li>
            <li>
              <Link className="anchor" to="#">
                Condition
              </Link>
            </li>
            <li>
              <Link className="anchor" to="#">
                Contact
              </Link>
            </li>
            <li>
              <Link className="anchor" to="#">
                Help
              </Link>
            </li>
          </ul>
          <p className="copyright-text">
            Copyright @
            <Link className="anchor" to="#">
              Axcer Innovation (pvt) Ltd.
            </Link>
            | All Right Reserved 2021
          </p>
        </div>
      </Container>
    </FotterWrapper>
  )
}

export default Footer
